import { Component } from "react";


export class Treat extends Component {
    componentDidMount() {
        
        document.addEventListener('mousemove', this.onMouseMove);
    }

    componentWillUnmount(){

        document.removeEventListener('mousemove', this.onMouseMove)            
    }

    onMouseMove (e) {
        let gw = document.querySelector(".game-window")
        let gwLeft = gw.getBoundingClientRect().left;
        let treat = document.getElementById('treat')
        var x = e.x - gwLeft - 20
        var y = e.y - 20
        treat.style.left = x + "px";
        treat.style.top = y + "px";
        // console.log(`x cords are ${x} and y cords are ${y}`)
    }

    render() {
        return <div id="treat">
            <img onClick={this.props.onFeedPet} src='https://cdn.pixabay.com/photo/2020/11/28/11/25/cookie-5784367_960_720.png' />
        </div>
    }
}